.chat__input{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 1rem 2rem;
    border-top: 1px solid lightgray;
    background-color: #f5f5f5;
}

.chat__input > form{
    -ms-flex: 1 1;
        flex: 1 1;
}

.chat__input > form > input{
    width: 98%;
    outline: none;
    border: 1px solid lightgray;
    border-radius: 999px;
    padding: 5px;
}

.chat__input > form > button{
    display: none;
}


